<template>
	<view class="box">

		<!-- 返回按钮 -->
		<view class="back" @click="handleBack">
			<image src="@/static/img/icon_back@3x.png" mode=""></image>
		</view>

		<!-- 签到按钮 -->
		<view class="attendance" @click="handleSignIn">
			<image src="@/static/img/tab_sign-in@3x.png" mode=""></image>
		</view>

		<!-- 轮播图 -->
		<view class="top">
			<swiper class="img" circular :autoplay="true" :interval="3000" :duration="1000">
				<swiper-item class="img-item" v-for="(item,index) in dataList.Banner" :key="index">
					<!-- <image class="item-img" src="/static/img/1.jpeg" mode="aspectFill"></image> -->
					<image class="item-img" :src="`https://farm.juzhentech.com${item.Image}`" mode="aspectFill"></image>
				</swiper-item>
			</swiper>
			<view class="tabs">
				<view class="tabs-text">
					<view class="" v-for="(item,index) in tabs" :key="index" @click="handleTabs(index)">{{item}}
						<view :class="[{'border': index===tabIndex}]"></view>
					</view>
				</view>
			</view>
		</view>

		<!-- 内容区域 -->
		<scroll-view scroll-y class="avtivit-list" @scrolltolower="handleLower">
			<information v-for="(item,index) in informationList" :key="index" :order="item" :name="Name">
			</information>
			<view v-if="informationList.length === 0" class="nono-list">
				————&nbsp&nbsp&nbsp&nbsp暂无数据&nbsp&nbsp&nbsp&nbsp————
				<!-- {{lower?'————&nbsp&nbsp&nbsp&nbsp暂无数据&nbsp&nbsp&nbsp&nbsp————':`————&nbsp&nbsp&nbsp&nbsp已加载完成&nbsp&nbsp&nbsp&nbsp————`}} -->
			</view>
			<view class="">

			</view>
		</scroll-view>

		<!-- 发布任务按钮 -->
		<view class="publish" v-if="dataList.IsRelease" @click="handlePublish">
			<image src="@/static/img/tab_release@3x.png" mode=""></image>
		</view>

		<!-- 签到弹框 -->
		<uni-popup ref="popup" type="bottom">
			<view class="popup">
				<view class="title">
					<view class="text">签到</view>
					<image src="@/static/img/icon_close@3x.png" mode="" @click="$refs.popup.close()"></image>
				</view>
				<view class="date-time">
					<view class="tiao">
						<text></text>
						<text v-if="signInData.List.length>1"
							:style="{width: `${90/signInData.ContinuousSign*signInData.List.length}%`} "></text>
					</view>
					<view class="imgs">
						<view class="img">
							<image src="@/static/img/success@3x.png" v-for="(item,index) in signInData.List.length"
								:key="index" mode="">
							</image>
							<image src="@/static/img/success@3x_1.png"
								v-for="(item,index) in (signInData.ContinuousSign-signInData.List.length-1)"
								:key="index" mode="">
							</image>
							<image src="@/static/img/icon_farming@3x.png" mode=""></image>
						</view>
						<view class="date-number">
							<view class="" v-for="(item,index) in (signInData.ContinuousSign)" :key="index">
								<view class="">{{index+1}}天</view>
							</view>
						</view>

					</view>
				</view>
				<view class="count">{{signInData.DetailStr}}</view>
				<view class="rules">
					<view class="rules-title">签到规则</view>
					<!-- <view class="rules-content" v-for="(item,index) in 4">{{index+1}}、规则内容</view> -->
					<view class="">{{signInData.SignRule}}</view>
				</view>
				<view class="btn" @click="handleSign">{{signInData.IsSign?'已签到':'确定'}}</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import {
		GetSignInlAjax,
		GetUserSignAjax,
		PostActivityIndexAjax
	} from '@/apis/api/modules/activity'
	import Information from './components/information/index.vue'
	export default {
		components: {
			Information
		},
		data() {
			return {
				Name: '',
				tabs: ['热门活动', '免费活动', '付费活动'],
				tabIndex: 0,
				qiandaoIndex: 90,
				dataList: null,
				informationList: [],
				signInData: '',
				PageIndex: 1,
				lower: true
			}
		},
		onLoad(e) {
			this.Name = e.Name
			this.getData()
		},
		methods: {
			async handleLower() {
				// if (!this.lower) {
				// 	return false
				// }
				this.PageIndex = this.PageIndex + 1

				const result = await PostActivityIndexAjax({
					ActivityType: this.tabIndex,
					PageIndex: this.PageIndex,
					PageSize: 4,
					token: true
				})
				if (result.Result.List.length > 0) {
					this.lower = true
					this.informationList.push(...result.Result.List)
					return false
				}

				this.lower = false
			},

			async getData() {
				const result = await PostActivityIndexAjax({
					ActivityType: this.tabIndex,
					PageIndex: this.PageIndex,
					PageSize: 4,
					token: true
				})
				if (result.Result.List.length === 0) {
					this.lower = true
				}
				this.dataList = result.Result
				this.informationList = result.Result.List
			},
			handleTabs(i) {
				this.PageIndex = 1
				this.tabIndex = i
				this.getData()
			},
			/**
			 * 上车
			 */
			handleTrain() {
				console.log('上车')
			},
			/**
			 * 返回上一页
			 */
			handleBack() {
				uni.navigateBack({
					delta: 1
				})
			},
			/**
			 * 获取签到数据详情
			 */
			async handleSignIn() {
				this.$refs.popup.open()
				const result = await GetSignInlAjax({
					token: true
				})
				this.signInData = result.Result
			},
			async handleSign() {
				if (!this.signInData.IsSign) {
					await GetUserSignAjax({
						token: true
					})
					uni.showToast({
						title: '签到成功！',
						icon: 'success'
					})
					this.$refs.popup.close()
					return false
				}
				this.$refs.popup.close()
			},
			/**
			 * 发布任务
			 */
			handlePublish() {
				uni.navigateTo({
					url: '/pages/activity/publish-activity/index'
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		display: flex;
		flex-direction: column;
		height: 100%;
	}

	.top {
		height: 640rpx;
	}

	.back {
		position: absolute;
		top: 66rpx;
		left: 30rpx;
		z-index: 20;

		image {
			width: 44rpx;
			height: 44rpx;
		}
	}

	.attendance {
		position: absolute;
		top: 130rpx;
		right: 30rpx;
		z-index: 20;

		image {
			width: 110rpx;
			height: 130rpx;
		}
	}

	.publish {
		position: absolute;
		bottom: 32rpx;
		right: 32rpx;

		image {
			width: 110rpx;
			height: 130rpx;
		}
	}

	.img {
		height: 600rpx;
		border-radius: 0 0 120rpx 120rpx;

		.img-item {
			width: 100%;
			height: 100%;
			position: relative;

			.item-img {
				left: 0;
				top: 0;
				position: absolute;
				width: 100%;
				// border-radius: 0 0 120rpx 120rpx;
				height: 100%;
			}
		}
	}

	.tabs {
		position: relative;
		top: -100rpx;
		padding: 0 30rpx;
		box-sizing: border-box;

		.tabs-text {
			box-shadow: 0px 4px 20px 0px rgba(0, 100, 69, 0.07);
			height: 104rpx;
			line-height: 104rpx;
			background: #FFFFFF;
			border-radius: 20rpx;
			display: flex;
			justify-content: space-between;
			padding: 0 56rpx;
			font-size: 32rpx;
			font-weight: 500;
			color: #333333;
			position: relative;

			.border {
				position: absolute;
				bottom: 30rpx;
				width: 130rpx;
				height: 18rpx;
				background: linear-gradient(180deg, rgba(0, 234, 194, 0) 0%, #00D08F 100%);
			}

		}
	}

	.avtivit-list {
		flex: 1;
		overflow: auto;
		height: calc(100vh - var(--window-top));
		padding: 30rpx;
		box-sizing: border-box;

		.nono-list {
			text-align: center;
			color: #c1c1ba;
			padding: 30rpx 0;
		}
	}

	.popup {
		background: #fff;
		padding: 36rpx 30rpx 40rpx;
		border-radius: 30rpx 30rpx 0px 0px;
		box-sizing: border-box;

		.title {
			display: flex;
			justify-content: end;
			align-items: center;

			.text {
				width: 90%;
				text-align: center;
			}

			image {
				width: 48rpx;
				height: 48rpx;
			}
		}

		.date-time {
			position: relative;
			box-sizing: border-box;
			height: 102rpx;
			width: 690rpx;

			.tiao {
				width: 690rpx;
				display: flex;
				justify-content: certen;
				align-items: center;
				position: relative;
				margin-top: 72rpx;

				text:nth-of-type(1) {
					position: absolute;
					left: 20px;
					width: 90%;
					height: 6px;
					background: #E9EBF5;
					border-radius: 6rpx;
				}

				text:nth-of-type(2) {
					position: absolute;
					left: 20px;
					height: 6px;
					background: #00D08F;
					border-radius: 6rpx;
				}
			}

			.imgs {
				position: absolute;
				top: -72rpx;
				left: 0;

				.img {
					width: 690rpx;
					padding: 30rpx 0 0rpx;
					height: 80rpx;
					display: flex;
					justify-content: space-between;
					align-items: center;

					image {
						width: 48rpx;
						height: 48rpx;
					}

					image:last-child {
						width: 72rpx;
						height: 72rpx;
					}
				}

				.date-number {
					width: 664rpx;
					// padding: 52rpx 0 30rpx;
					display: flex;
					justify-content: space-between;
					align-items: center;
				}
			}
		}

		.count {
			text-align: center;
			font-size: 24rpx;
			font-weight: 400;
			color: #999999;
			line-height: 34rpx;
			padding-bottom: 30rpx;
		}

		.rules {
			background: #F7F7F7;
			border-radius: 20rpx;
			padding: 20rpx;
			font-size: 28rpx;
			line-height: 40rpx;

			.rules-title {
				font-weight: 500;
				color: #333333;
				padding-bottom: 20rpx;
			}

			.rules-content {
				font-weight: 400;
				color: #666666;
				padding-bottom: 20rpx;

				&:last-child {
					padding-bottom: 0rpx;
				}
			}
		}

		.btn {
			margin-top: 40rpx;
			padding: 30rpx 0;
			background: #00D08F;
			border-radius: 52rpx;
			backdrop-filter: blur(20rpx);
			font-size: 32rpx;
			font-weight: 500;
			color: #FFFFFF;
			text-align: center;
			line-height: 44rpx;
		}
	}
</style>
